"use client";
import React, { useEffect, useState } from "react";
import useAuthStore from "@/store/authStore";
import useDonationStore from "@/store/donationStore";
import useCampaignStore from "@/store/campaignStore";
import { userService } from "@/services/userService";
import { couponService } from "@/services/couponService";

export default function UserDashboard() {
  const { user } = useAuthStore();
  const { getUserDonations, userDonations } = useDonationStore();
  const { getUserCampaigns, userCampaigns } = useCampaignStore();
  
  const [dashboardData, setDashboardData] = useState({
    totalDonation: 0,
    totalCampaigns: 0,
    totalRaised: 0,
    totalDonations: 0,
    totalCoupons: 0,
    activeCampaigns: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchDashboardData = async () => {
      if (!user) return;

      try {
        setLoading(true);

        // Fetch all data in parallel (without stats endpoint since it doesn't exist)
        const [
          donationsResponse,
          campaignsResponse,
          couponsResponse
        ] = await Promise.allSettled([
          getUserDonations(),
          getUserCampaigns(),
          couponService.getUserCoupons().catch(() => ({ data: [] }))
        ]);

        // Wait a bit for store to update
        await new Promise(resolve => setTimeout(resolve, 100));

        // Get updated data from store
        const currentDonations = useDonationStore.getState().userDonations || [];
        const currentCampaigns = useCampaignStore.getState().userCampaigns || [];

        // Calculate total donations
        let totalDonation = 0;
        if (donationsResponse.status === 'fulfilled') {
          totalDonation = currentDonations.reduce((sum, donation) => sum + (donation.amount || 0), 0);
        }

        // Calculate campaign stats
        let totalCampaigns = 0;
        let totalRaised = 0;
        let activeCampaigns = 0;
        
        if (campaignsResponse.status === 'fulfilled') {
          totalCampaigns = currentCampaigns.length;
          totalRaised = currentCampaigns.reduce((sum, campaign) => sum + (campaign.amountRaised || 0), 0);
          activeCampaigns = currentCampaigns.filter(c => c.status === 'active' || c.status === 'approved').length;
        }

        // Get coupons count
        let totalCoupons = 0;
        if (couponsResponse.status === 'fulfilled' && couponsResponse.value?.data) {
          totalCoupons = Array.isArray(couponsResponse.value.data) ? couponsResponse.value.data.length : 0;
        }

        // Update dashboard data (calculated from actual data)
        setDashboardData({
          totalDonation: totalDonation,
          totalCampaigns: totalCampaigns,
          totalRaised: totalRaised,
          totalDonations: currentDonations.length,
          totalCoupons: totalCoupons,
          activeCampaigns: activeCampaigns,
        });

      } catch (error) {
        console.error("Failed to fetch dashboard data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchDashboardData();
  }, [user, getUserDonations, getUserCampaigns]);

  // Update when userDonations changes
  useEffect(() => {
    if (userDonations && userDonations.length > 0) {
      const total = userDonations.reduce((sum, donation) => sum + (donation.amount || 0), 0);
      setDashboardData(prev => ({
        ...prev,
        totalDonation: total,
        totalDonations: userDonations.length
      }));
    }
  }, [userDonations]);

  // Update when userCampaigns changes
  useEffect(() => {
    if (userCampaigns && userCampaigns.length > 0) {
      const totalRaised = userCampaigns.reduce((sum, campaign) => sum + (campaign.amountRaised || 0), 0);
      const activeCampaigns = userCampaigns.filter(c => c.status === 'active' || c.status === 'approved').length;
      setDashboardData(prev => ({
        ...prev,
        totalCampaigns: userCampaigns.length,
        totalRaised: totalRaised,
        activeCampaigns: activeCampaigns
      }));
    }
  }, [userCampaigns]);

  return (
    <div className="py-6">
      {/* Heading */}
      <div className="mb-6">
        <h1 className="text-3xl font-bold text-blue-900 mb-2">
          Welcome To Care Foundation Trust
        </h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li>
              <a href="/dashboard" className="hover:underline text-gray-600 hover:text-blue-600">
                Home
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Dashboard</li>
          </ol>
        </nav>
      </div>

      {/* Donation Card */}
      <div className="bg-white rounded-lg shadow-md border border-gray-200 p-8 mb-6">
        <div className="flex items-center justify-center gap-8">
          {/* Green Circle with Rupee Symbol */}
          <div className="w-32 h-32 bg-green-600 rounded-full flex items-center justify-center shadow-lg flex-shrink-0">
            <span className="text-white text-5xl font-bold">‚Çπ</span>
          </div>

          {/* Donation Amount */}
          <div>
            <p className="text-gray-600 text-sm font-medium mb-2">Total | Donation</p>
            <h2 className="text-6xl font-bold text-gray-900">
              {loading ? (
                <span className="text-4xl">Loading...</span>
              ) : (
                dashboardData.totalDonation.toLocaleString()
              )}
            </h2>
          </div>
        </div>
      </div>

      {/* Additional Stats Cards - Optional */}
      {(dashboardData.totalCampaigns > 0 || dashboardData.totalRaised > 0 || dashboardData.totalCoupons > 0) && (
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {dashboardData.totalCampaigns > 0 && (
            <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
              <p className="text-gray-600 text-sm font-medium mb-2">My Campaigns</p>
              <h3 className="text-3xl font-bold text-blue-900">
                {dashboardData.totalCampaigns}
              </h3>
              {dashboardData.activeCampaigns > 0 && (
                <p className="text-xs text-green-600 mt-1">
                  {dashboardData.activeCampaigns} Active
                </p>
              )}
            </div>
          )}

          {dashboardData.totalRaised > 0 && (
            <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
              <p className="text-gray-600 text-sm font-medium mb-2">Total Raised</p>
              <h3 className="text-3xl font-bold text-green-600">
                ‚Çπ{dashboardData.totalRaised.toLocaleString()}
              </h3>
            </div>
          )}

          {dashboardData.totalCoupons > 0 && (
            <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
              <p className="text-gray-600 text-sm font-medium mb-2">My Coupons</p>
              <h3 className="text-3xl font-bold text-purple-600">
                {dashboardData.totalCoupons}
              </h3>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

